.container{
    background: white;
    text-align: center;
    width: 746px;
    height: 576pxpx;
    padding: 12px 33px;
    margin-left: 391px;
    margin-top: 54px;
    border-radius: 38px
}
.container h1{
    font-size: 27px;
    color: black;
}
.container hr{
    color: #00dbff;
}
.container h2{
    font-size: 22px;
    font-family: 'Times New Roman', Times, serif;
    font-weight: 100;
    padding-bottom: 20px;
}
.container button{
    width: 392px;
  height: 41px;
  border-radius: 5px;
  background-color: rgb(43, 37, 155);
  color: white;
  font-size: 27px;
  cursor: pointer;
}
.container ul li{
    display: flex;
    align-items: center;
    height: 70px;
    padding-left: 15px;
    border: 1px solid #686868;
    border-radius: 8px;
    font-size: 20px;
    margin-bottom: 10px;
    cursor: pointer;
}
.container .wrong{
    background: #FFEBEB;
    border-color: #FF4A4A;
}
.container .correct{
    background: #dffff2;
    border-color: #00d397;
}